import React , {Component} from 'react';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Back from '@material-ui/icons/ArrowBackIos';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import CreatePractical from './createPractical'
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import LinearProgress from '@material-ui/core/LinearProgress';
 class PracticalSubmission extends Component {
 	constructor(props) {
 		super(props)
 		this.state={
            list:[],
            data:[],
            lab:[],
            showL:true,
            index:0,
            load:false
    }
        this.getLabs(props.user.id) 
     }
    handleShowL=(index)=>{
      if(index){this.setState({index})
      this.setState({showL:!this.state.showL})
    }else{ this.setState({index:0,showL:!this.state.showL})
  }
      }
  getLabs=(id)=>{
    this.props.db.collection("tLab")
     .doc(id)    
    .get()
    .then(doc=> {
        console.log(doc)
        if(doc.exists===true){
        this.setState({lab:doc.data().data})
        doc.data().data.map(item=>{
            this.getLabsResult(item.subject)
        })
        }
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }
  getLabsResult=(sub)=>{
    var data=this.state.data
    var list=[]
  this.props.db.collection("practicalResult")
   .doc(sub)    
  .get()
  .then(doc=> {
      if(doc.exist!==true){
          var obj={
              sub:sub,
              data:doc.data()        
          }
        data.push(obj)
        this.setState({data})
      }
  })
  .catch(function(error) {
      console.log("Error getting documents: ", error);
  });
}
openCreatePrac=()=>{
  this.setState({openCreate:!this.state.openCreate})
}
 	render(){
     const {db, slideBar, handleSlideBar, user, Subjectdata, setSnachBarData}=this.props
     const {list, data, openCreate}=this.state;
     console.log(data)
 	return (
    !data.length>0?<LinearProgress color="secondary" />:
     <div className='tc mt3' style={{height:'100%'}}>
       <CreatePractical open={openCreate} getLabsData={this.getLabsData} close={this.openCreatePrac} db={db} setSnachBarData={setSnachBarData} id={user.id} Subjectdata={Subjectdata} />
    {!this.state.showL? <IconButton className='fl ml3' onClick={this.handleShowL}>
    <Back/> 
     </IconButton>:null}
     <Button color="primary" >
     Practical Submissions
   </Button>
  {data.map(item=>{
      return(<List>
           <Button color="primary" >
     {item.sub}
   </Button>
     {Object.keys(item.data).map((key, index) => (
         <div>
        <ExpansionPanel>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                <Typography >{key}</Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                {Object.keys(item.data[key]).map((key1, ind) => (
                     <ExpansionPanel style={{width:'100%'}}>
                     <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                     <Typography >{key1}</Typography>
                     </ExpansionPanelSummary>
                     <ExpansionPanelDetails>
                        <div>
                        <Button color="primary" >
                            Que:
                        </Button>{item.data[key][key1].question}
                        </div>
                            <br/>
                        <div>
                                   <Button color="primary" >
                                   Output:
                                    </Button>
                            
                            {Object.keys(item.data[key][key1].output).map((key2, i ) =>(
                                <pre>
                                   
                                    {key2}:{item.data[key][key1].output[key2]}
                                </pre>
                            ))}
                            <br/>
                            <Button color="primary" >
                                   Code:
                                    </Button>
                            <pre style={{width:'100%',background:"gray", color:'white',borderRadius:5,padding:10}}>
                                   
                            {item.data[key][key1].code}
                            </pre>
                        </div>
                     </ExpansionPanelDetails>
                 </ExpansionPanel>
                ))}
                </ExpansionPanelDetails>
      </ExpansionPanel>
       
       <Divider />
       </div>
     ))}
   </List>
      )
  })}
    </div>
 		);
}
}
export default PracticalSubmission;