import React , {Component} from 'react';
import Card from './Card/card' 
import CircularProgress from '@material-ui/core/CircularProgress';
import './home.css'
import Nav from './Component/Bar/nav'
import Search from './search'
import SmallSlider from "./Component/Slider/SmallSlider";
import Slider from './Component/Slider/Slider'
import OtherSlider from './Component/Slider/otherSlider'
import Scroll from './Component/Bar/scroll'
 class Home extends Component {
 	constructor() {
 		super()
 		this.state={
      data:[
        {a:0},
        {b:0},
        {c:0},
        {a:0},
        {b:0},
        {c:0},
        
      ]      
    }
          
    }
   

 	render(){
     const {db}=this.props
 	return (
     <div className='' style={{height:'100%'}}>
       
 <div className='back back-wrap'>
 <Nav handleSlideBar={this.props.handleSlideBar}/>
 <Scroll handleSlideBar={this.props.handleSlideBar}/>
    <div className='gradient'>
    </div>
 <div className='container'>
   <div className='hex'>
          <div className="pt5">
          <p class="mb4 head1">Find Shop, work, and get job done</p>
          <h1 class="mb5 head2">The Eassiest Way to get Shop</h1>
         <Search/>
          </div>
   </div>
 </div>
</div>
<SmallSlider db={db.collection("catagory")}/>
<Slider db={db.collection("Banner")}/>
<OtherSlider db={db.collection("SmallAd")}/>
</div>
 		);
}
}
export default Home;