import React , {Component} from 'react';
import './App.css';
import NavBar from './Components/layout/Bar/NavBar'
import Group from './Components/group/group'
import Home from './Components/layout/Home/Home'
import Tabs from './Components/group/Tab'
import Scroll from './Components/layout/Bar/scroll'
import SlideBar from './Components/Slidebar'
import Login from './Components/layout/Login/login'
import SnachBar from './Components/layout/Bar/SnachBar/SnachBBar'
import { Route } from "react-router-dom";
import Page from './Components/layout/Page/page'
import Footer from './Components/layout/Footer/footer'
import Admin from './Components/Admin'
import Teacher from './Components/layout/teacher/teacher.js'
import Student from './Components/layout/student/student'
import Playground from './Components/Playground/playground'
const firebase = require("firebase");

// Required for side-effects
require("firebase/firestore");

// Initialize Cloud Firestore through Firebase
firebase.initializeApp({
    apiKey: "AIzaSyDFE1sQeMGaDmZV_r2oBCE2IzaoiT_3Cl4",
    authDomain: "https://accounts.google.com/o/oauth2/auth",
    projectId: "gcoeatest"
  });
  
var db = firebase.firestore();
 class App extends Component {
 	constructor(props) {
 		super(props)
 		this.state={
				 slideBar: false,
				 Auth:false,
				 userData:{},
				 LoginDialog:true,
				 snachBarOpen:false,
				
					}			
	}
	componentDidMount(){
		this.stableRoute()
	}
	
	getUser=(data)=>{
		console.log(data,this.props)
		this.setState({userData:data})
		if(data.save){
			localStorage.setItem('login',JSON.stringify(data));
		}
		this.setState({Auth:true})
		this.handlelogin()
		if(data.type==="t"){
			this.props.history.push(`/teacher`)
		}else{
			this.props.history.push(`/student`)
		}
	}
	stableRoute=()=>{
		var obj =JSON.parse(localStorage.getItem('login'))
		if(obj!=="" && obj!==null){
			this.getUser(obj)
		}	else{
			this.props.history.push(`/`)	
		}
	}
	handleSlideBar=()=>{
		this.setState({slideBar:!this.state.slideBar})
	}
	logout=()=>{
		localStorage.removeItem('login')
		this.props.history.push(`/`)
	}
	handlelogin=()=>{
		this.setState({LoginDialog:!this.state.LoginDialog})
	}
	handleSnach=()=>{
		this.setState({snachBarOpen:!this.state.snachBarOpen})
	}
	setSnachBarData=(type,data)=>{
		var obj={}
		obj['v']=type
		obj['msg']=data
		this.setState({snachBarData:obj})
		this.handleSnach()
	}
 	render(){
		 const {slideBar, Auth,userData,LoginDialog,snachBarOpen, snachBarData}=this.state;
 	return (
 <div className='tc'>
 
{window.location.pathname!=='/playground'?<NavBar user={userData} logout={this.logout} handleSlideBar={this.handleSlideBar} {...this.props} position="static" Auth={Auth} handlelogin={this.handlelogin}/>
:null}
 <Route path="/page" exact component={Page} />
<Route path='/admin' exact render={()=>(<Admin db={db}/>)}/>
<Route path='/teacher'  render={(props)=>(<Teacher db={db} {...props} Auth={Auth} slideBar={slideBar} setSnachBarData={this.setSnachBarData} handleSlideBar={this.handleSlideBar} user={userData}/>)}/>
<Route path='/student'  render={(props)=>(<Student db={db} {...props} Auth={Auth} slideBar={slideBar} setSnachBarData={this.setSnachBarData} handleSlideBar={this.handleSlideBar} user={userData}/>)}/>
<Route path='/playground'  render={(props)=>(<Playground db={db} setSnachBarData={this.setSnachBarData} {...props} user={userData}/>)}/>
<Route exact path='/' render={()=>(
 <Login open={true} handlelogin={this.handlelogin} db={db} getUser={this.getUser} setSnachBarData={this.setSnachBarData}/>)}/>
 <SnachBar open={snachBarOpen} data={snachBarData} handleSnach={this.handleSnach}/>

</div>
 		);
}
}
export default App;