import React , {Component} from 'react';
import Table from './Table'
 class Catagory extends Component {
 	constructor() {
 		super()
 		this.state={
            Catagory:[],
            catagoryColumns: [
                { title: 'Url', field: 'url' },
                { title: 'ImageUrl', field: 'image_url', type: 'image' },
                { title: 'Id', field: 'id' },
               
              ]
    }    
    }
   
    async deleteCatagory(data){
        console.log(data)
    await this.props.db.doc(data.id)
    .delete().then(function() {
        console.log("Document successfully deleted!");
    }).catch(function(error) {
        console.error("Error removing document: ", error);
    });
    this.getCatagory()
    }   
 async getCatagory(){
    var Catagory=[]
    await this.props.db
   .get()
   .then(function(querySnapshot) {
       console.log(querySnapshot)
   querySnapshot.forEach(function(doc) {
       var d={}
       d=doc.data()
       d['id']=doc.id
        Catagory.push(d)
    });
})
.catch(function(error) {
    console.log("Error getting documents: ", error);
});

this.setState({Catagory})
}
async add(data){
    const {db} =this.props;
    var d= {}
    d=data
   await db.add(data)
    .then(function(docRef) {
    })
    .catch(function(error) {
        console.error("Error adding document: ", error);
    });
    
    this.getCatagory()
}

async edit(data){
    const {db} =this.props;
   await db.doc(data.id).set(data)
    .then(function(docRef) {
    })
    .catch(function(error) {
        console.error("Error adding document: ", error);
    });
    
    this.getCatagory()
}
editRow=(data,type)=>{
   if(type==='add') this.add(data)
   if(type==='delete') this.deleteCatagory(data)
   if(type==='edit') this.edit(data)
}

componentDidMount(){
    this.getCatagory()
}

 	render(){
         const {Catagory, catagoryColumns}= this.state
 	return (
    <div>
        <Table title='Banner' columns={catagoryColumns} data={Catagory} editRow={this.editRow} />
    </div>
 		);
}
}
export default Catagory;