import React , {Component} from 'react';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Back from '@material-ui/icons/ArrowBackIos';
import IconButton from '@material-ui/core/IconButton';
import { Promise } from 'q';
import LinearProgress from '@material-ui/core/LinearProgress';
 class Tests extends Component {
 	constructor(props) {
 		super(props)
 		this.state={
            list:[],
            data:[],
            showL:true,
            index:0
    }
         this.getTests() 
    }
    handleShowL=(index)=>{
      console.log(index)
      if(index){this.setState({index})
      this.setState({showL:!this.state.showL})
    }else{ this.setState({index:0,showL:!this.state.showL})
  }
      }
  getTests=()=>{
      var data=[]
      var list=[]
    this.props.db.collection("testData").where("subject", "==",this.props.Subjectdata.subject )
    .get()
    .then(querySnapshot=> {
      var d=  querySnapshot.forEach(function(doc) {
            data.push(doc.data())
            list.push(doc.data().testName)
        });
        Promise.all(d)
        this.setState({data:data})
        this.setState({list})
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }
  stopLive=(index)=>{
    this.props.db.collection("testData").doc(this.state.data[index].testName)
    .update({
        live:false,
        completed:true,
        completionDate:Date()
    }).then(d=>{
       this.props.db.collection("testLive").doc(this.state.data[index].testName).delete()
       .then(d=> {
        this.props.setSnachBarData("success", "Stopped the Test successfully")
        this.getTests()
      
        })
    }).catch(function(error) {
        this.props.setSnachBarData("error", "Error stoping please try again")
        });
  }
  goLive=(index)=>{
    this.props.db.collection("testLive").doc(this.state.data[index].testName).set(this.state.data[index])
    .then(t=> {
      this.props.setSnachBarData("success", "test have been started")
      this.props.db.collection("testData").doc(this.state.data[index].testName)
      .update({
          live:true
      }).then(d=>{
          this.getTests()
      })
   
    })
    .catch(t=> {
        this.props.setSnachBarData("error", "Error please try again")
    })
  }
 	render(){
     const {db, slideBar, handleSlideBar, user}=this.props
     const {list, data}=this.state;
 	return (
    !data.length>0?<LinearProgress color="secondary" />:
     <div className='tc mt3' style={{height:'100%'}}>
    {!this.state.showL? <IconButton className='fl ml3' onClick={this.handleShowL}>
    <Back/> 
     </IconButton>:null}
     <Button color="primary" >
     Test Data
   </Button>
    {this.state.showL? <List>
     {list.map((text, index) => (
         <div>
       <ListItem  key={text} >
         <ListItemText primary={text} />
        {!this.state.data[index].live?<Button color="primary" disabled={this.state.data[index].completed} onClick={()=>{this.goLive(index)}}>
         Go Live 
       </Button>:<Button color="primary" disabled={this.state.data[index].completed} onClick={()=>{this.stopLive(index)}}>
       Stop Test
     </Button>} <Button color="Secondary" onClick={()=>this.handleShowL(index)}>
       View
     </Button>
       </ListItem>
       <Divider />
       </div>
     ))}
   </List>:null}
    </div>
 		);
}
}
export default Tests;