import React from 'react';
import Slider from "react-slick";
import Skeleton from '@material-ui/lab/Skeleton';



 class SmallSlider extends React.Component {

  state={
    data:{}
}
componentDidMount(){
    console.log(this.state.data)
    if(!this.state.data.length>0){
    this.getData()
    }
}
async getData(){
  var Catagory=[]
  await this.props.db
 .get()
 .then(function(querySnapshot) {
 querySnapshot.forEach(function(doc) {
     var d={}
     d=doc.data()
      Catagory.push(d)
  });
})
.catch(function(error) {
  console.log("Error getting documents: ", error);
});
this.setState({data:Catagory})
}
  render() {
    const categorySlider  = {

        speed: 600,
        autoplay:true,
        autoplaySpeed: 2000,
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    const {data}=this.state;
    return (
     <div className="sliderA flex flex-column">
           <h4 className='ml5-l'>
                  Popular Shops
                </h4>
       <Slider className="slider w-70-l w-90"  style={{margin:'5px', marginLeft:"auto", marginRight:'auto'}} {...categorySlider} >
      
         
          
            {data.length>0?data.map(item=>{
       return(  <div className=''>
       <img src='  https://rukminim1.flixcart.com/flap/1688/280/image/2b89c50d90584a21.jpg?q=50' height='300px'/>
        </div>)
      }):(
        [0,1,2,3].map(item=>{
          return(
        <Skeleton width='100%' height='300px' /> 
          )
        })
      )}
        </Slider>
     </div>

    );  
  }
} 
export default SmallSlider