import React from 'react';
import MaterialTable from 'material-table';

export default function MaterialTableDemo(props) {
  const [state, setState] = React.useState({
    columns: [
      { title: 'Name', field: 'name' },
      { title: 'Surname', field: 'surname' },
      { title: 'Birth Year', field: 'birthYear', type: 'numeric' },
      {
        title: 'Birth Place',
        field: 'birthCity',
        lookup: { 34: 'stanbul', 63: 'anlurfa' },
      },
    ],
    data: [
      { name: 'Mehmet', surname: 'Baran', birthYear: 1987, birthCity: 63 },
      {
        name: 'Zerya Betl',
        surname: 'Baran',
        birthYear: 2017,
        birthCity: 34,
      },
    ],
  });

  return (
    <MaterialTable
      title={props.title}
      columns={props.columns}
      data={props.data}
      editable={{
        onRowAdd: newData =>
          new Promise(resolve => {
              resolve();
            props.editRow(newData,'add') 
          }),
        onRowUpdate: (newData, oldData) =>
          new Promise(resolve => {
             resolve();
             props.editRow(newData,'edit')
          }),
        onRowDelete: oldData =>
          new Promise(resolve => {
            resolve();
           props.editRow(oldData, 'delete')
          }),
      }}
    />
  );
}
