import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Close from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import withMobileDialog from '@material-ui/core/withMobileDialog';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import Card from '@material-ui/core/Card';
import Clock from './Clock'
import LinearProgress from '@material-ui/core/LinearProgress';
const firebase = require("firebase");
var jwt = require('jsonwebtoken');



function MadeWithLove() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
    
    </Typography>
  );
}

const useStyles = theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(1),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  fab: {
    margin: theme.spacing(1),
    width:'50px',
    height:'50px'
}
});

class SignIn extends React.Component {
    state={
      data:[],
      pass:'',
      type:'email',
      checked:true,
      year:'',
      name:'',
      subject:'',
      d:[],
      answered:{},
      Load:true
    }
    submit=()=>{
      this.setState({Load:true})
      var info={}
      var mark= this.props.data.marks/this.props.data.data.length
      this.props.db.collection("testResult").doc(this.props.data.testName)
      .get()
      .then(doc=>{
    if(doc.data().data.filter(vendor => (vendor.id === this.props.user.id)).length===0){
  var result =0
  console.log(this.state.d)
  var data = this.props.data.data
  var p=data&&data.map((da, index)=>{
 var decoaded = jwt.verify(da, 'info');
 if(decoaded.m1===this.state.answered[index]){
  result++
}
})
result=result*mark
  Promise.all(p)
  info['name']=this.props.user.name
  info['id']=this.props.user.id
  info['marks']=result
  console.log(result)
  this.props.db.collection("testResult").doc(this.props.data.testName)
  .update({
    data: firebase.firestore.FieldValue.arrayUnion(info)
  }).then(d=>{
    this.setState({Load:false})
    this.props.setSnachBarData("success", `You got ${result} marks`)
    this.props.handlemcq()
  }).catch(e=>{
    this.setState({Load:false})
   this.props.setSnachBarData("error", `Error please try again`)
 
  })
 
}else{
  this.setState({Load:false})
  this.props.setSnachBarData("error", 'Sorry, Test cannot be given twice')
  this.props.handlemcq()
 
}
      })
    
    }
   convertData=(newProps)=>{
     this.setState({Load:true})
     if(newProps.data.data){
  var data= []
  data=newProps.data.data
   var data1=[]
var d=data&&data.map(da=>{
  var decoaded = jwt.verify(da, 'info');
  data1.push(decoaded)
})
  Promise.all(d)
  this.setState({d:data1})
  var d4=data1.map(d3=>{
    
   var que=d3.que
    delete d3.que
    delete d3.iat
    var result = Object.keys(d3).map(function(key) {
      return d3[key];
    });
    function shuffle(array) {
      array.sort(() => Math.random() - 0.5);
    }
    shuffle(result);
    d3['ans']=result
    delete d3.m1
    delete d3.m2
    delete d3.m3
    delete d3.m4
    d3['que']=que
  })
  Promise.all(d4)
  this.setState({data:data1,Load:false})
   }
  }
   componentWillReceiveProps(newProps){
    if(newProps.data&&this.state.Load) this.setState({Load:false})
    
    if(this.state.data.length===0) this.convertData(newProps)
  
   }
    handleChange=( index, event )=> {
       var answered=[]
      answered=this.state.answered
      answered[index]=event.target.value
      this.setState({answered:answered})
      console.log(answered, index)
    }
    handleCheck=()=>{
      this.setState({checked:!this.state.checked})
      console.log(this.state.checked)
    }
    render(){
        const {classes, fullScreen, data}=this.props;
        const {checked, answered, Load}=this.state;
        console.log(answered)
        return (
   <Dialog
    fullScreen={true}
    open={this.props.open}
    onClose={this.props.handlemcq}
    aria-labelledby="responsive-dialog-title"
  >
  {Load===true? <LinearProgress   variant="query" color="secondary" />
  :null}
        <CssBaseline />
      <div className={classes.paper}>

      <DialogTitle id="responsive-dialog-title">
      <Card style={{position:'fixed',top:'0', margin:'10px',right:0}}> { data?<Clock submit={this.submit} time={data.time}/>:null}</Card>
        <Typography component="h1" variant="h5" color='primary'>
         MCQ Test
        </Typography>
      </DialogTitle>
      
       
       <div className={classes.form}>
        <DialogContent>
        {data?<div className='tc flex ml-auto mr-auto'> 
        <Typography variant="h6" style={{margin:'7px', fontSize:'15px'}} color="secondary" gutterBottom>Year: {data.year}</Typography>
        <Typography variant="h6" style={{margin:'7px', fontSize:'15px'}} gutterBottom color="secondary">Test Name: {data.testName}</Typography>
        <Typography variant="h6" style={{margin:'7px', fontSize:'15px'}} gutterBottom color="secondary">Sem: {data.sem}</Typography>     
        <Typography variant="h6" style={{margin:'7px', fontSize:'15px'}} gutterBottom color="secondary">Total Marks: {data.marks}</Typography>     
        </div>:null}</DialogContent>
       {this.state.data.length>0? this.state.data.map((item, index)=>{
           return(
            <Card style={{padding:'10px',margin:'8px'}}>
          <Typography variant="h6" gutterBottom>
        Q.{index+1}  {item.que} ?
        </Typography>
        <RadioGroup  aria-label="type" name="type" value={this.state.answered[index]} onChange={(event)=>{this.handleChange(index,event)}}>
         {item.ans.map(option=>{
          return(
            <FormControlLabel
            value={option}
            control={<Radio color="primary" />}
            label={option}
            labelPlacement="end"
          />
          )
         }) }
        </RadioGroup>
        </Card> 
           )
         }):null}   
        
        </div> 
        <div className='flex'>
        <Button variant="contained" color='primary' onClick={this.submit}>
            Submit
        </Button>
       </div>
      </div>
      <Box mt={5}>
        <MadeWithLove />
      </Box>
      </Dialog>
  )}
}
var sign = withStyles(useStyles)(SignIn)
export default withMobileDialog()(sign)