import React , {Component} from 'react';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Back from '@material-ui/icons/ArrowBackIos';
import IconButton from '@material-ui/core/IconButton';
import Table from './table'
import { Promise } from 'q';
 class TestResult extends Component {
 	constructor(props) {
 		super(props)
 		this.state={
            list:[],
            data:[],
            showL:true,
            index:0
    }
         this.getTests() 
    }
    handleShowL=(index)=>{
      console.log(index)
      if(index){this.setState({index})
      this.setState({showL:!this.state.showL})
    }else{ this.setState({index:0,showL:!this.state.showL})
  }
      }
  getTests=()=>{
      var data=[]
      var list=[]
    this.props.db.collection("testResult").where("subject", "==",this.props.Subjectdata.subject )
    .get()
    .then(querySnapshot=> {
      var d=  querySnapshot.forEach(function(doc) {
            data.push(doc.data())
            list.push(doc.data().testName)
        });
        Promise.all(d)
        this.setState({data:data})
        this.setState({list})
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }
 	render(){
     const {db, slideBar, handleSlideBar, user}=this.props
     const {list, data}=this.state;
     console.log(data)
 	return (
     <div className='tc mt3' style={{height:'100%'}}>
    {!this.state.showL? <IconButton className='fl ml3' onClick={this.handleShowL}>
    <Back/> 
     </IconButton>:null}
     <Button color="primary" >
     Test Result
   </Button>
    {this.state.showL? <List>
     {list.map((text, index) => (
         <div>
       <ListItem button key={text} onClick={()=>this.handleShowL(index)}>
         <ListItemText primary={text} />
       </ListItem>
       <Divider />
       </div>
     ))}
   </List>:<Table data={this.state.data[this.state.index]}/>}
    </div>
 		);
}
}
export default TestResult;