import React , {Component} from 'react';
import Intro from '../card/intro'
import Button from '@material-ui/core/Button';
import CreateMcq from '../test/cretaemcqTest'
import SlideBar from '../../Slidebar'
import { Route } from "react-router-dom";
import TestResult from './TestResult/TestResult'
import Tests from './Tests/tests'
import Practical from './practical/practical'
import PracticalSubmission from './practical/practicalSubmission'
import Table from './table/table'
import CssBaseline from '@material-ui/core/CssBaseline';
import { withStyles } from '@material-ui/core/styles';
var List=["Home",'Add Test', 'Tests',  "Practicals","Practical Submissions"]

const styles = theme => ({
  root: {
    display: 'flex',
  },
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
  },
});

 class Teacher extends Component {
 	constructor(props) {
 		super(props)
 		this.state={
     createMcq:false,
     Subjectdata:{},
     load:false,
     testResult:[],
     currentData:[],
     info:{}
    }
}
    getTestResults=(sub)=>{
      var d=[]
      d=this.state.testResult
      this.props.db.collection("testResult").where("subject", "==", sub)
        .get()
        .then(querySnapshot=> {
        querySnapshot.forEach(function(doc) {
              d.push(doc.data())
              console.log(doc.id, " => ", doc.data());
          });
          if(d.length>0)  this.setState({testResult:d,currentData:d[0].data,info:d[0]})          
          else this.setState({testResult:[]})
        }).catch(function(error) {
        alert(error)
         });
  
    }
    changeCurrent=(cu)=>{
      var value=cu.target.value
      this.setState({currentData:value.data,info:value})
    }
    getSubjects=(id)=>{
        var d={}
        this.props.db.collection("tSubject")
        .doc(id)
        .get()
          .then(doc => {
          d=doc.data()
    this.setState({Subjectdata:d})
        d.data.map(item=>{this.getTestResults(item.subject)})
        }).catch(function(error) {
         
          alert(error)
         });
        }  
   
    handlemcq=()=>{
       this.setState({createMcq:!this.state.createMcq})
   }
   onSlideSubmit=(text)=>{
    if(text==='Home') this.props.history.push('/teacher')
     if(text==='Add Test') this.handlemcq()
     if(text==='Tests') this.props.history.push('/teacher/tests')
     if(text==='Add Practical') this.props.history.push('/teacher/addPractical')
     if(text==='Practicals') this.props.history.push('/teacher/practical')
     if(text==='Practical Submissions') this.props.history.push('/teacher/practicalSubmission')
    if(this.props.slideBar) this.props.handleSlideBar()
   }

   componentWillReceiveProps(newProps){
    if(newProps.user.id&&!this.state.load){
      this.setState({load:true})
      this.getSubjects(newProps.user.id)
    }
 
   }
 	render(){
     const {db, slideBar, handleSlideBar, user,Auth, setSnachBarData, classes}=this.props
     const {createMcq, Subjectdata,testResult, currentData, info,load}=this.state;
     console.log(testResult)
 	return (
     Auth?<div className='' style={{height:'100%',display:'flex'}}>
       <CssBaseline/>
        <SlideBar token="IT Professor" open={slideBar} handleSlideBar={handleSlideBar} user={user} List={List} onSlideSubmit={this.onSlideSubmit}/>
        <div style={{ flexGrow: 1,padding: 10}}>
         <Intro user={user}/>
         <Route path='/teacher' exact render={()=>( <Table changeCurrent={this.changeCurrent} info={info} testResult={testResult} data={currentData}/>)}/>
         <CreateMcq db={db} open={createMcq} setSnachBarData={setSnachBarData} handlemcq={this.handlemcq} id={user.id} Subjectdata={Subjectdata}/>
        <Route path='/teacher/tests' exact render={()=>(<Tests db={db} setSnachBarData={setSnachBarData} Subjectdata={Subjectdata.data[0]}/>)}/>
         <Route path='/teacher/practical' exact render={()=>(<Practical db={db} setSnachBarData={setSnachBarData} Subjectdata={Subjectdata.data[0]} user={user}/>)}/>
         <Route path='/teacher/practicalSubmission' exact render={()=>(<PracticalSubmission db={db} setSnachBarData={setSnachBarData} Subjectdata={Subjectdata.data[0]} user={user}/>)}/>
         </div>
    </div>:null
 		);
}
}
export default withStyles(styles, { withTheme: true })(Teacher);