import React , {Component} from 'react';
import Catagory from './Component/Catagory'
import Banner from './Component/Banner'
import SmallAd from './Component/SmallAd'

 class Admin extends Component {
 	constructor() {
 		super()
 		
    }
 

 	render(){
         const {db}= this.props
 	return (
    <div>
        <h1>Admin Panel</h1>
        <Catagory db={db.collection("catagory")}/>
        <Banner db={db.collection("Banner")}/>
        <SmallAd db={db.collection("SmallAd")}/>

    </div>
 		);
}
}
export default Admin;