import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Close from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import withMobileDialog from '@material-ui/core/withMobileDialog';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import IconButton from '@material-ui/core/IconButton';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import Card from '@material-ui/core/Card';
import CreateCard from './createCard'
var jwt = require('jsonwebtoken');


function MadeWithLove() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
    
    </Typography>
  );
}

const useStyles = theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  fab: {
    margin: theme.spacing(1),
    width:'50px',
    height:'50px',
    marginLeft:'auto',
    marginRight:'auto'
},
textField:{
    margin:'7px',
    width:'70%'
},
textField1:{
    margin:'7px',
    width:'30%'
}
});

class MCQ extends React.Component {
    
    constructor(props){
      super(props);
      this.state={
        checked:true,
        name:'',
        subject:'',
        data:[],
        subAray:[],
        time:'10:00',
        marks:0
  
      }
      this.getSubjects()
    }
    submit=()=>{
        var data=[]
   var s=  this.state.data.map(item=>{
       var token = jwt.sign(item, 'info');
          data.push(token)
     })
     Promise.all(s)
     console.log(data)
     var d={}
     d['completed']=false
     d['creationDate']=Date()
     d['marks']=this.state.marks
     d['sem']=this.state.subAray[this.state.subject].sem
     d['year']=this.state.subAray[this.state.subject].year
     d['subject']=this.state.subAray[this.state.subject].subject
     d['testName']=this.state.name
     d['data']=data
     d['live']=false
     d['time']=this.state.time
     this.props.db.collection("testData").doc(this.state.name).set(d)
  .then(t=> {
    var d={}
    d['data']=[]
    d['marks']=this.state.marks
    d['sem']=this.state.subAray[this.state.subject].sem
    d['year']=this.state.subAray[this.state.subject].year
    d['subject']=this.state.subAray[this.state.subject].subject
    d['testName']=this.state.name
    this.props.db.collection("testResult").doc(this.state.name).set(d)
    this.props.setSnachBarData("success", "Successfully added test")
    this.props.handlemcq()
  })
  .catch(t=> {
    this.props.setSnachBarData("error", "Error adding test")
   })
    }
   getSubjects=()=>{
    this.props.db.collection("tSubject").doc(this.props.id)
    .get()
    .then(doc=> {
        this.setState({subAray:doc.data().data})
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
   }
    handleChange = name => event => {
      this.setState({[name]:event.target.value})
    }
    handleCheck=()=>{
      this.setState({checked:!this.state.checked})
      console.log(this.state.checked)
    }
    handleRadioChange=(event)=>{
      this.setState({value:event.target.value})
    }
    closeQue=(item)=>{
      var data =this.state.data
      var filtered = data.filter(function(value, index, arr){
        return value!==item;
    });
    this.setState({data:filtered})
    }
    handleData=(name,i)=>event=>{
        var data =[]
        data=this.state.data
        var obj=data[i]
        if(name==='que'){
            obj['que']=event.target.value
        }
        if(name==='m1'){
            obj['m1']=event.target.value
        }
        if(name==='m2'){
            obj['m2']=event.target.value
        }
        if(name==='m3'){
            obj['m3']=event.target.value
        }
        if(name==='m4'){
            obj['m4']=event.target.value
        }
        data[i]=obj
        this.setState({data})
    }
    Add=()=>{
        var data =[]
        data=this.state.data
        var obj={}
        data.push(obj)
        this.setState({data})
    }
    render(){
        const {classes, fullScreen, handlemcq}=this.props;
        const {checked, data, subAray}=this.state;
        console.log(this.state)
        return (
    <Dialog
    fullScreen={true}
    open={this.props.open}
    onClose={this.props.handlemcq}
    aria-labelledby="responsive-dialog-title"
  >
      <IconButton style={{width:'50px'}} onClick={handlemcq}>
          <Close/>
          </IconButton>
        <CssBaseline />
      <div className={classes.paper}>

      
        <Typography component="h1" variant="h5">
         Create MCQ Test
        </Typography>
     
       
        <div className={classes.form}>
        <DialogContent >
        <FormControl className={classes.formControl}>
        <InputLabel htmlFor="age-simple">For Year</InputLabel>
        <Select
          value={this.state.subject}
          onChange={this.handleChange('subject')}
          inputProps={{
            name: 'Year',
            id: 'age-simple',
          }}
        >
        {subAray.map((item,index)=>{
          return(
            <MenuItem value={index}>{item.subject} {item.year} year</MenuItem>
          )
        })}
        </Select>
      </FormControl>
     
      <TextField
        label="Test Name"
        variant="outlined"
        className={classes.formControl}
        value={this.state.name}
        onChange={this.handleChange('name')}
      /><TextField
      label="Total Marks"
      variant="outlined"
      className={classes.formControl}
      value={this.state.marks}
      onChange={this.handleChange('marks')}
    />
         <form className={classes.container} noValidate>
      <TextField
        id="time"
        label="Time"
        className={classes.formControl}
        type="time"
        defaultValue="10:00"
        onChange={this.handleChange('time')}
         InputLabelProps={{
          shrink: true,
        }}
        inputProps={{
          step: 300, 
        }}
      />
    </form>
          </DialogContent>
         { data&&data.map((item,i)=>{
          return( <CreateCard data={data} handleData={this.handleData} closeQue={this.closeQue} i={i} item={item}/>)
          })  } <div className='w-100 tc'>
         <Fab color="primary" aria-label="add" className={classes.fab} onClick={this.Add}>
        <AddIcon />
      </Fab></div>
        </div>
        <div className='flex'>
        <Button color='primary' onClick={this.submit}>
            Submit
        </Button>
        <Button color='secondary' onClick={handlemcq}>
            Cancel
        </Button></div>
      </div>
      <Box mt={5}>
        <MadeWithLove />
      </Box>
      </Dialog>
  )}
}
var sign = withStyles(useStyles)(MCQ)
export default withMobileDialog()(sign)