import ImageGallery from 'react-image-gallery';
import React from 'react'
import "react-image-gallery/styles/scss/image-gallery.scss";
import "react-image-gallery/styles/css/image-gallery.css";
import "react-image-gallery/styles/css/image-gallery.css";
const img1 = 'https://nhs-dynamic.secure.footprint.net/Images/Homes/NVRInc/33939922-190523.jpg'
const img2 = 'https://nhs-dynamic.secure.footprint.net/Images/Homes/NVRInc/33939922-190523.jpg'
const img3 ='https://nhs-dynamic.secure.footprint.net/Images/Homes/NVRInc/33939922-190523.jpg'

//# Stylesheet with no icons
//node_modules/react-image-gallery/styles/scss/image-gallery-no-icon.scss
//node_modules/react-image-gallery/styles/css/image-gallery-no-icon.css 
export default class MyComponent extends React.Component {


  render() {

    const images = [
      {
        original:img1 ,
        thumbnail: img1,
      },
      {
        original: img2,
        thumbnail: img2
      },
      {
        original: img3,
        thumbnail: img3
      }
    ]

    return (
      <ImageGallery showPlayButton={false} showBullets={true} autoPlay={true} showFullscreenButton={false} showThumbnails={true} items={images} />
    );
  }

}