import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import PlayIcon from '@material-ui/icons/PlayArrow';
import Submit from '@material-ui/icons/CheckCircle';
import Tooltip from '@material-ui/core/Tooltip';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    backgroundColor:'white'
  },
}));

export default function ButtonAppBar(props) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <AppBar position="static" style={{backgroundColor:'#28282a'}}>
        <Toolbar>
       
          <FormControlLabel
        control={
          <Switch
            checked={props.theme}
           onChange={props.changeTheme}
            value={props.theme}
          />
        }
        label="Dark Mode"
      />
      <FormControl  className={classes.formControl}>
      <InputLabel shrink id="demo-simple-select-placeholder-label-label">
      Language
    </InputLabel>
      <Select 
      onChange={props.onLanguageSelect}
      value={props.language}
     >
      <MenuItem value='c'>C</MenuItem>
      <MenuItem value='cpp'>Cpp</MenuItem>
      <MenuItem value='java'>Java</MenuItem>
      <MenuItem value='node'>Javascript</MenuItem>
      <MenuItem value='python'>Python</MenuItem>
    </Select>
    </FormControl>
          <Typography variant="h6" className={classes.title}>
            Playground
          </Typography>
          <Tooltip title="Run" aria-label="run">
          <IconButton edge="end" className={classes.menuButton} color="inherit" onClick={props.onPlay} disabled={props.Load}>
            <PlayIcon />
          </IconButton>
          </Tooltip>
          <Tooltip title="Submit" aria-label="run">
          <IconButton edge="end" className={classes.menuButton} color="inherit" onClick={props.submitOutput} disabled={!props.submit}>
            <Submit />
          </IconButton>
          </Tooltip>
          </Toolbar>
      </AppBar>
    </div>
  );
}