import React from 'react';
import Slider from "react-slick";
import Card from "./card";
import Skeleton from '@material-ui/lab/Skeleton';



 class SmallSlider extends React.Component {

  state={
    data:{}
}
componentDidMount(){
    console.log(this.state.data)
    if(!this.state.data.length>0){
       this.getCatagory()
    }
}
async getCatagory(){
  var Catagory=[]
  await this.props.db
 .get()
 .then(function(querySnapshot) {
 querySnapshot.forEach(function(doc) {
     var d={}
     d=doc.data()
      Catagory.push(d)
  });
})
.catch(function(error) {
  console.log("Error getting documents: ", error);
});
this.setState({data:Catagory})
}
  render() {
    const categorySlider  = {
      dots: false,
      infinite: false,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 3,
      responsive: [
          {
            breakpoint: 1300,
            settings: {
              slidesToShow: 5,
            }
          },
          {
            breakpoint: 1260,
            settings: {
              slidesToShow: 3,
            }
          },
      ]
    };
    const {data}=this.state;
    return (
     <div className="parent_scrollbar w-70-l w-100">
         
       <Slider className="category_slider  "  style={{margin:'5px', marginLeft:"auto", marginRight:'auto'}} {...categorySlider} >
      {data.length>0?data.map(item=>{
       return( <Card item={item}/>)
      }):(
        [0,1,2,3].map(item=>{
          return(
        <div style={{padding:'2px'}}>
        <Skeleton width={210} height={200} /> 
        </div>
          )
        })
      )}
        </Slider>
     </div>
    );  

  }
} 
export default SmallSlider