import React, { Component } from "react";
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';


export default class Navbar extends Component {
  constructor(props) {
    super(props);

    this.state = {      
      visible: false,
    };
  }

  // Adds an event listener when the component is mount.
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  // Remove the event listener when the component is unmount.
  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  // Hide or show the menu.
  handleScroll = () => {
   var visible=false
    if(window.pageYOffset>80){
       visible=true
    }else{
        visible=false
    }
    this.setState({
      visible
    });
  }

  render() {
      const {visible}=this.state;
    return visible?
                   (    <div className=' bg-black scrollContainer '>
                   <div className='dn-l'>     <IconButton edge="start"  color="inherit" aria-label="menu" style={{float:'right', color:'white', marginRight:'15px'}} onClick={this.props.handleSlideBar}>
                           <MenuIcon className='dn-l' />
                         </IconButton></div>
                                       <div className='fr w-60 dn db-l' >
                     <Button  className='nav-button' style={{ color:'white',padding:'10px', fontWeight: '400', fontSize:'13px', margin:'10px'}}>
                       Home
                     </Button>
                     <Button  className='nav-button' style={{ color:'white',padding:'10px', fontWeight: '400', fontSize:'13px', margin:'10px'}}>
                       Secondary
                     </Button><Button  className='nav-button' style={{color:'white', padding:'10px', fontWeight: '400', fontSize:'13px', margin:'10px'}}>
                       Secondary
                     </Button><Button  className='nav-button' style={{color:'white', padding:'10px', fontWeight: '400', fontSize:'13px', margin:'10px'}}>
                       Secondary
                     </Button><Button  className='nav-button' style={{color:'white', padding:'10px', fontWeight: '400', fontSize:'13px', margin:'10px'}}>
                       Contact Us
                     </Button>
                     <Button variant="contained" color="secondary" style={{color:'white', padding:'10px', fontWeight: '400', fontSize:'13px', margin:'10px'}}>
                       Post Ad
                     </Button>   
                     </div>           </div>):null

  }
}