import React, { Component } from 'react';
import Card from '@material-ui/core/Card';

export default class detailCard extends Component {
	state={
		open:false
	}
	handleShare=()=>{
		this.setState({open:!this.state.open})
	}
	render() {
		const {data}=this.props;
		return (<div className=''>
			<Card className={{borderRadius:'0px', padding:'10px'}} style={{padding:'5px'}}>
		<div className='dib fl  ma2 '>	<span className='f6 black-90 '>{data.name}</span></div>
		<br/>
			<div className='dib ma2 mt1'><span className='rating white br3 f7 pa1 pr1 pt1 pl1 fl'>{data.rating} &#9734;</span><span className='f7 mid-gray pl2'>80,864 ratings, 9,113 reviews</span> <span><img className='v-mid' src='data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCI+PHBhdGggZmlsbC1vcGFjaXR5PSIuNTQiIGZpbGwtcnVsZT0iZXZlbm9kZCIgZD0iTTguNiA3LjRMMTAgNmw2IDYtNiA2LTEuNC0xLjQgNC42LTQuNnoiLz48L3N2Zz4='/> </span></div>
		<br/>
			<div> <span className='grclr f7 ml2'>  Extra 2000 discount</span></div>
			<div className='f4 ma2'><span className='pr1 b black-80'> {data.price}</span><span className='strike mid-gray pr1 f6'> {data.price+10}</span><span className='grclr pr1 f6'> {Math.round(1000/data.price)}% off</span> </div>
			</Card><div className='mt1'><Card><div onClick={this.handleShare} className='center h2 v-mid pt2 tc mid-gray' style={{height:'50px',paddingTop:'15px'}}><img className='arrow' src='data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSI1MTIiIGhlaWdodD0iNTEyIiB2aWV3Qm94PSIwIDAgNTEyIDUxMiI+PHBhdGggZmlsbD0iI2ExYTFhMSIgZD0iTTUxMiAzMmMwIDY0LTk2IDE5Mi0zMjAgMTkyVjk2TDAgMjg4bDE5MiAxOTJWMzUyYzE3Ni40MzggMCAzMjAtMTQzLjU2MiAzMjAtMzIweiIvPjwvc3ZnPg=='/>Share </div></Card></div></div>
		);
	}
}
