import React , {Component} from 'react';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Back from '@material-ui/icons/ArrowBackIos';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import CreatePractical from './createPractical'
import LinearProgress from '@material-ui/core/LinearProgress';
 class Practicals extends Component {
 	constructor(props) {
 		super(props)
 		this.state={
            list:[],
            data:[],
            lab:[],
            showL:true,
            index:0,
            load:false
    }
        this.getLabs(props.user.id) 
     }
    handleShowL=(index)=>{
      if(index){this.setState({index})
      this.setState({showL:!this.state.showL})
    }else{ this.setState({index:0,showL:!this.state.showL})
  }
      }
  getLabs=(id)=>{
    this.props.db.collection("tLab")
     .doc(id)    
    .get()
    .then(doc=> {
        console.log(doc)
        if(doc.exists===true){
            console.log(doc.data())
        this.setState({lab:doc.data().data})
        doc.data().data.map(item=>{
            this.getLabsData(item.subject)
        })
        }
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }
  getLabsData=(sub)=>{
    var data=this.state.data
    var list=[]
  this.props.db.collection("practical")
   .doc(sub)    
  .get()
  .then(doc=> {
      if(doc.exist!==true){
        data.push(doc.data())
        this.setState({data})
      }
  })
  .catch(function(error) {
      console.log("Error getting documents: ", error);
  });
}
openCreatePrac=()=>{
  this.setState({openCreate:!this.state.openCreate})
}
 	render(){
     const {db, slideBar, handleSlideBar, user, Subjectdata, setSnachBarData}=this.props
     const {list, data, openCreate}=this.state;
 	return (
    !data.length>0?<LinearProgress color="secondary" />:
     <div className='tc mt3' style={{height:'100%'}}>
       <CreatePractical open={openCreate} getLabsData={this.getLabsData} close={this.openCreatePrac} db={db} setSnachBarData={setSnachBarData} id={user.id} Subjectdata={Subjectdata} />
    {!this.state.showL? <IconButton className='fl ml3' onClick={this.handleShowL}>
    <Back/> 
     </IconButton>:null}
     <Button color="primary" >
     Current Practicals
   </Button>
   <Fab color="primary" aria-label="add" style={{margin:'20px'}} onClick={this.openCreatePrac}>
        <AddIcon />
      </Fab>
  {data.map(item=>{
      return(<List>
           <Button color="primary" >
     {item.subject}
   </Button>
     {item.data.map((text, index) => (
         <div>
       <ListItem button key={text} >
         <ListItemText primary={index+1+". "+text.question} />
       </ListItem>
       <Divider />
       </div>
     ))}
   </List>
      )
  })}
    </div>
 		);
}
}
export default Practicals;