import React, { Component } from 'react';
import Card from '@material-ui/core/Card';

export default class Highlights extends Component {
	render() {
		const {data}=this.props;
		var array=data.description?data.description.split("."):null

		return (<div className='mt1'>
			<Card>
		<div className='dib fl  ma2 '>	<span className='f5 pl2 fw5' style={{color:'rgb(135, 135, 135)'}}>Highlights</span></div>
		<br/>
			<div className=' ma2 ml3 f6 black-80 mt4'>
		{array?array.map((item)=>{
			return item!==''?<li className='lh-copy'>{item}</li>:null
		})
			
		:null}	

			</div>

			<Card><div className=' fw5 mt2 f6 mb2 pa2'>All Details<img className='v-mid fr' src='data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIyNCIgaGVpZ2h0PSIyNCI+PHBhdGggZmlsbC1vcGFjaXR5PSIuNTQiIGZpbGwtcnVsZT0iZXZlbm9kZCIgZD0iTTguNiA3LjRMMTAgNmw2IDYtNiA2LTEuNC0xLjQgNC42LTQuNnoiLz48L3N2Zz4='/></div></Card></Card></div>
		);
	}
}
