import React , {Component} from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';

 class search extends Component {
 	constructor() {
 		super()
          
    }
    
 	render(){
         	return (
    <div className='search-container'>
        <div>
         <TextField
        id="standard-name"
        label="Shop Name"
        margin="normal"
        variant="outlined"
        style={{marginBottom:'16px',marginRight:'10px',width:'70%'}}
      />
     
      <Button variant="contained" color="primary" style={{marginTop:'16px', fontSize:'15px'}}>
       Search
      </Button>
    </div>
    </div>
 		);
}
}
export default search;