import React , {Component} from 'react';
import Intro from '../card/intro'
import SlideBar from '../../Slidebar'
import Mcq from '../test/mcqTest'
import { Route } from "react-router-dom";
import LiveTests from './Test/liveTests'
import Practicals from './practical/practical'
import Playground from '../../Playground/playground'
import CssBaseline from '@material-ui/core/CssBaseline';
import { withStyles } from '@material-ui/core/styles';
var List=["Home",'Live Tests','Test Results', 'Tests',"Practicals",'Playground']
const styles = theme => ({
  root: {
    display: 'flex',
  },
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
  },
});

 class Student extends Component {
 	constructor() {
 		super()
 		this.state={
     showMcq:false,
     mcqData:{}
    }
          
    }
   handlemcq=(data)=>{
    this.setState({
      showMcq:!this.state.showMcq,
      mcqData:data
    })
   }
    onSlideSubmit=(text)=>{
      if(text==='Home') this.props.history.push('/Student')
      if(text==='Live Tests') this.props.history.push('/Student/liveTests')
      if(text==='Test Results') this.props.history.push('/Student/testResult')
      if(text==='Tests') this.props.history.push('/Student/tests')
      if(text==='Playground') this.props.history.push('/playground')
      if(text==='Practicals') this.props.history.push('/student/practicals')
     if(this.props.slideBar) this.props.handleSlideBar()
    }
 	render(){
     const {db, user, slideBar, handleSlideBar, Auth, setSnachBarData}=this.props
     const {showMcq, mcqData}=this.state;
 	return (
     Auth?<div className='' style={{height:'100%',display:'flex'}}>
        <CssBaseline/>
        <SlideBar token="IT Student"  open={slideBar} handleSlideBar={handleSlideBar} user={user} List={List} onSlideSubmit={this.onSlideSubmit}/> 
         <div style={{ flexGrow: 1,padding: 10}}>
         <Intro user={user}/>
         <Mcq db={db} open={showMcq} handlemcq={this.handlemcq}  data={mcqData} user={user} setSnachBarData={setSnachBarData}/>
         <Route path='/student/liveTests' exact render={()=>(<LiveTests handlemcq={this.handlemcq} db={db} year={user.year}/>)}/>
         <Route path='/student/practicals' exact render={(props)=>(<Practicals handlemcq={this.handlemcq} db={db} sem={user.sem} {...props}/>)}/>
      </div>
    </div>:null
 		);
}
}
export default withStyles(styles, { withTheme: true })(Student);