import React from "react";
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    textField: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
      width: 200,
    },
  }));
export default class Playground extends React.Component{
    constructor(props){
        super(props)
    }


    render(){
        const {output, onInputChange}=this.props
        return(
            <div className='h-100 w-40 relative flex-' >
            <Typography component="h5" variant="h6" >
        Output:
        </Typography>
       {output.memoryUsage? <div >{output.stdout!==""?<pre style={{overflowX:'auto',overflowY:'auto',padding:'20px'}}>{output.stdout}</pre>
    :<div>
    <pre style={{overflowX:'auto',overflowY:'auto',padding:'20px'}}>{output.stderr}</pre>
    <br/>
    Error type: {output.errorType}
    </div>}
        </div>:null}
        <TextField
          id="filled-textarea"
          label="Inputs"
          placeholder="Click run after providing complete inputs"
          multiline
          style={{width:'100%',bottom:0}}
          onChange={onInputChange}
          margin="normal"
          variant="filled"
        />
        </div>
    )
        
    }
}