import React,{ useRef} from "react";
import {ControlledEditor} from '@monaco-editor/react';
import Output from './output'
import Bar from './bar';
import LinearProgress from '@material-ui/core/LinearProgress';
import QuestionCard from './questionCard'
import firebase from 'firebase'
var jwt = require('jsonwebtoken');

export default class Playground extends React.Component{
    constructor(props){
        super(props)
        this.state={
            language:'c',
            theme:true,
            value:'',
            output:{},
            input:'',
            Load:false,
            question:{},
            submit:false
        }
       
    }
    componentDidMount(){
        if(this.props.location.search&&!this.state.question.question){
            var str=this.props.location.search
            str=str.substring(1)
            var decoded = jwt.verify(str, 'shhhhh');
            this.setState({question:decoded})
        }
        console.log(this.props.user)
    }
     handleEditorDidMount=(ev,value)=> {
        this.setState({value,submit:false})
      }
      onInputChange=(event)=>{
          this.setState({input:event.target.value})
      }
      onPlay=()=>{
          this.setState({Load:true})
        fetch('http://localhost:3001/runcode',{
            method:'post',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify({
            code:this.state.value,
            input:this.state.input,
            language:this.state.language
          })
          }).then(response=>response.json())
            .then(output=>
              {
                  this.setState({output:output,
                Load:false})
                if(output.stderr===""&&this.state.question.question){
                    this.setState({submit:true})
                }
                console.log(output)
              })
      }
onLanguageSelect=(event)=>{
this.setState({language:event.target.value})
}
changeTheme=()=>{
    this.setState({theme:!this.state.theme})
}
submitOutput=()=>{
    this.setState({Load:true})
    var {question}=this.state
   var d={}
   d['output']=this.state.output
   d['question']=question.question
   d['code']=this.state.value
  
this.props.db.collection("practicalResult").doc(question.subject)
.update({
    [this.props.user.id]: {
        [question.PracticalName]:d
    }
}).then(d=>{
  this.setState({Load:false})
  this.props.setSnachBarData("success", `Your practical have successfully submited`)
}).catch(e=>{
  this.setState({Load:false})
  console.log(e)
 this.props.setSnachBarData("error", `Error please try again`)

})


    
  

}
    render(){
        const {language,theme,value,output,Load, question,submit}=this.state;
        return(
            <div>
            <Bar language={language}
            onLanguageSelect={this.onLanguageSelect}
            changeTheme={this.changeTheme}
            theme={theme}
            onPlay={this.onPlay}
            Load={Load}
            submit={submit}
            submitOutput={this.submitOutput}/>
            {Load===true? <LinearProgress   variant="query" color="secondary" />
     :null}
            <div className='flex w-100' key={language}>
            <ControlledEditor
            value={value}
            height="100vh"
            language={language}
            theme={theme?'dark':'light'}
            onChange={this.handleEditorDidMount}
             />
            {question.question? <QuestionCard question={question}/>:null}
             <Output output={output}
            theme={theme?'dark':'light'}
             onInputChange={this.onInputChange}/>
            </div>
            </div>
        )
    }
}