import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import withMobileDialog from '@material-ui/core/withMobileDialog';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import LinearProgress from '@material-ui/core/LinearProgress';

const bcrypt = require('bcrypt-nodejs')


function MadeWithLove() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
    
    </Typography>
  );
}

const useStyles = theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    maxWidth:700,
    marginLeft:'auto',
    marginRight:'auto'
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
});

class SignIn extends React.Component {
    state={
      id:'',
      pass:'',
      type:'email',
      checked:false,
      value:"",
      Load:false

    }
    submitLogin=()=>{
      this.setState({Load:true})
     if(this.state.value===""){
      this.setState({Load:false})
       this.props.setSnachBarData("error","Select an option either Teacher or Student")
     }else{
        const {db, setSnachBarData}=this.props;
        var data = '';
        db.collection(this.state.value==="t"?"tuser":"suser")
        .doc(this.state.id.toString())
        .get()
          .then(doc => {
            if(doc.exist===false){
              this.setState({Load:false})
            setSnachBarData("error", "Wrong credentials")
          return  
          }
              if(this.state.pass=== doc.data().hash){
                data=doc.data()
                data['type']=this.state.value
                data['save']=this.state.checked
               this.props.getUser(data)
               this.setState({Load:false})
               setSnachBarData("success", "Logged in successfully")
              }else{
                this.setState({Load:false})
            setSnachBarData("error", "Wrong credentials")
              }
              data=doc.data()
        
        }).catch(d=> {
          this.setState({Load:false})
          setSnachBarData("error", "Check internet connection")
        });
       }
     
    }
    handleChange = name => event => {
      this.setState({[name]:event.target.value})
    }
    handleCheck=()=>{
      this.setState({checked:!this.state.checked})
    }
    handleRadioChange=(event)=>{
      this.setState({value:event.target.value})
    }
    render(){
        const {classes, fullScreen}=this.props;
        const {checked,Load}=this.state;
        return (
    <Dialog
    fullScreen={true}
    open={this.props.open}
    onClose={this.props.handlelogin}
    aria-labelledby="responsive-dialog-title"
  >
  
      <CssBaseline />
     {Load===true? <LinearProgress   variant="query" color="secondary" />
     :null} <div className={classes.paper}>
      <DialogTitle id="responsive-dialog-title">
      <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Log in
        </Typography>
      </DialogTitle>
      
       
        <div className={classes.form}>
        <DialogContent>
     <div className='flex mr-auto ml-auto'>  <RadioGroup row aria-label="type" name="type" value={this.state.value} onChange={this.handleRadioChange}>
          <FormControlLabel
            value="t"
            control={<Radio color="primary" />}
            label="Teacher"
            labelPlacement="start"
          />
          <FormControlLabel
            value="s"
            control={<Radio color="secondary" />}
            label="Student"
            labelPlacement="start"
          />
         
        </RadioGroup>
        </div>  <TextField
            variant="outlined"
            margin="normal"
            onChange={this.handleChange('id')}
            required
            value={this.state.data}
            fullWidth
            id="email"
            label="Id"
            name="email"
            autoComplete="email"
            autoFocus
          />
          <TextField
            variant="outlined"
            onChange={this.handleChange('pass')}
            value={this.state.pass}
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
          />
          <FormControlLabel
            control={<Checkbox value={checked} onClick={this.handleCheck} color="primary" />}
            label="Remember me"
          />
          </DialogContent>
          <div className='pa3'>
          <Button
            onClick={this.submitLogin}
            fullWidth
            variant="contained"
            color="primary"
            disabled={Load}
            className={classes.submit}
          >
            Log In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href="#" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
          </div>
        </div>
      </div>
      <Box mt={5}>
        <MadeWithLove />
      </Box>
      </Dialog>
  )}
}
var sign = withStyles(useStyles)(SignIn)
export default withMobileDialog()(sign)