import React, { Component } from 'react'
import Card from '@material-ui/core/Card';
import IconButton from '@material-ui/core/IconButton';
import TextField from '@material-ui/core/TextField';
import Close from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

const useStyles = theme => ({
    '@global': {
      body: {
        backgroundColor: theme.palette.common.white,
      },
    },
    paper: {
      marginTop: theme.spacing(4),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    },
    avatar: {
      margin: theme.spacing(1),
      backgroundColor: theme.palette.secondary.main,
    },
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
    fab: {
      margin: theme.spacing(1),
      width:'50px',
      height:'50px',
      marginLeft:'auto',
      marginRight:'auto'
  },
  textField:{
      margin:'7px',
      width:'70%'
  },
  textField1:{
      margin:'7px',
      width:'30%'
  }
  });
  
 class card extends Component {
    render() {
        const {data, classes, handleData, closeQue, i,item}=this.props
        return(
       <Card style={{padding:'10px',margin:'5px'}}>
          <IconButton style={{width:'50px', float:'right'}} onClick={()=>{closeQue(item)}}>
          <Close/>
          </IconButton>
            <Typography variant="h6" gutterBottom>
          Q.{i+1}  <TextField
          id="standard-uncontrolled"
          placeholder='Write the Question'
          value={item.que}
          className={classes.textField}
          onChange={handleData('que',i)}
        /> ?
          </Typography>
          <div className='flex flex-column'>
          <TextField
          id="standard-uncontrolled"
          placeholder='the correct option'
          className={classes.textField1}
          value={item.m1}
          onChange={handleData('m1',i)}
        /> <TextField
        id="standard-uncontrolled"
        placeholder='option 2'
        className={classes.textField1}
        onChange={handleData('m2',i)}
        value={item.m2}
      /> <TextField
      id="standard-uncontrolled"
      placeholder='option 3'
      className={classes.textField1}
      value={item.m3}
      onChange={handleData('m3',i)}
    /> <TextField
    id="standard-uncontrolled"
    placeholder='option 4'
    className={classes.textField1}
    onChange={handleData('m4',i)}
    value={item.m4}
  /> </div>
  
          </Card> )
        
    }
}


var cards = withStyles(useStyles)(card)
export default cards