import React , {Component} from 'react';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import Back from '@material-ui/icons/ArrowBackIos';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Promise } from 'q';
import LinearProgress from '@material-ui/core/LinearProgress';
 class Tests extends Component {
 	constructor(props) {
 		super(props)
 		this.state={
            list:[],
            data:[],
            showL:true,
            index:0,
            open:false
    }
         this.getTests() 
    }
    handleShowL=(index)=>{
      console.log(index)
      if(index){this.setState({index})
      this.setState({showL:!this.state.showL})
    }else{ this.setState({index:0,showL:!this.state.showL})
  }
      }
  getTests=()=>{
      var data=[]
      var list=[]
    this.props.db.collection("testLive").where("year", "==",this.props.year)
    .get()
    .then(querySnapshot=> {
      var d=  querySnapshot.forEach(function(doc) {
            data.push(doc.data())
            list.push(doc.data().testName)
        });
        Promise.all(d)
        this.setState({data:data})
        this.setState({list})
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }
  onGoLiveClick=(index)=>{
      this.setState({
          open:!this.state.open,
          index
      })
  }
 	render(){
     const {db, slideBar, handleSlideBar, user, handlemcq}=this.props
     const {list, data, open}=this.state;
 	return (
    !list.length>0?<LinearProgress color="secondary" />:
     <div className='tc mt3' style={{height:'100%'}}>
    {!this.state.showL? <IconButton className='fl ml3' onClick={this.handleShowL}>
    <Back/> 
     </IconButton>:null}
     <Button color="primary" >
     Live Tests
   </Button>
    {this.state.showL? <List>
     {list.map((text, index) => (
         <div>
       <ListItem  key={text} >
         <ListItemText primary={text} />
         <Button color="primary" onClick={()=>{this.onGoLiveClick(index)}}>
         Start Test
       </Button> 
       </ListItem>
       <Divider />
       </div>
     ))}
   </List>:null}
   <Dialog
   open={open}
   onClose={()=>{this.setState({open:false})}}
   aria-labelledby="alert-dialog-title"
   aria-describedby="alert-dialog-description"
 >
   <DialogTitle id="alert-dialog-title">{"You are about to start the test!"}</DialogTitle>
   <DialogContent>
     <DialogContentText id="alert-dialog-description">
     {open? ` The test name is ${data[this.state.index].testName} for the year ${data[this.state.index].year} sem ${data[this.state.index].sem}.`:null
   }<Typography color="secondary"> Warming:If entered you cannot exit the test, do not close the browser or turn off internet else your marks will be uploaded as 0!</Typography>
     </DialogContentText>
   </DialogContent>
   <DialogActions>
     <Button onClick={()=>{this.setState({open:false})}} color="primary">
       Disagree
     </Button>
     <Button onClick={()=>{
       handlemcq(data[this.state.index]);
       this.setState({open:false})}} color="primary" autoFocus>
       Agree
     </Button>
   </DialogActions>
 </Dialog>
    </div>
 		);
}
}
export default Tests;